/* noteItem */

.item-enter {
  height: 0;
  left: -200px;
}

.item-enter-active {
  left: 0;
  height: 104px;
  transition: all 400ms cubic-bezier(0.25, 0.46, 0.45, 0.94);
}

.item-exit {
  z-index: -1;
  height: 104px;
  transform: translateY(0);
}

.item-exit-active {
  height: 0;
  transform: translateY(-104px);
  transition: all 400ms cubic-bezier(0.25, 0.46, 0.45, 0.94);
}

/* tag-item */

.tag-item-enter {
  height: 0;
  /* left: -200px; */
}

.tag-item-enter-active {
  /* left: 0; */
  height: 40px;
  transition: all 400ms cubic-bezier(0.25, 0.46, 0.45, 0.94);
}

.tag-item-exit {
  z-index: -1;
  height: 40px;
  transform: translateY(0);
}

.tag-item-exit-active {
  height: 0;
  transform: translateY(-40px);
  transition: all 400ms cubic-bezier(0.25, 0.46, 0.45, 0.94);
}

/* noteDetail */

.noteDetail-enter {
  left: -200px;
}

.noteDetail-enter-active {
  left: 0;
  transition: all 400ms cubic-bezier(0.25, 0.46, 0.45, 0.94);
}

.display-enter, .display-appear {
  top: var(--menu-starting-top);
  width: var(--toggler-width);
  max-height: var(--toggler-height);
  color: var(--fade-from-color);
  background-color: var(--toggler-bg-color);
}

.display-enter-active, .display-appear-active {
  top: var(--menu-ending-top);
  width: var(--menu-width);
  max-height: var(--menu-max-height);
  color: var(--fade-to-color);
  background-color: var(--menu-bg-color);
  transition: all var(--timeout);
}

/* page transition */

.page {
  position: absolute;
  left: 0;
  right: 0;
}

.page-enter {
  transform: translateX(100%);
}

.page-enter-active {
  transform: translateX(0%);
  transition: all 400ms cubic-bezier(0.25, 0.46, 0.45, 0.94);
}

.page-exit {
  transform: translateX(0%);
}

.page-exit-active {
  transform: translateX(-100%);
  transition: all 400ms cubic-bezier(0.25, 0.46, 0.45, 0.94);
}

